* { box-sizing:border-box; }

/* basic stylings ------------------------------------------ */
h2 		 { 
  text-align:center; 
  margin-bottom:50px; 
}
h2 small { 
  font-weight:normal; 
  color:#888; 
  display:block; 
}

/* form starting stylings ------------------------------- */
.group 			  { 
  position:relative; 
  display: flex;
  align-items: center;
}


.input-text-input-element				{
  font-size:18px;
  padding:10px 10px 10px 5px;
  display:block;
  width: 15em;
  border:none;
  background-color: transparent;
  color: white;
}

.input-text-input-element[type=text] {
  border-bottom:1px solid #757575;
}

.input-text-input-element[type=number] {
  width:60px;
  text-align: center;
  font-size: 1.7rem;
  font-weight: bold;
}

.input-text-input-element[type=number]::-webkit-inner-spin-button,
.input-text-input-element[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.input-text-input-element:focus 		{ outline:none; }

/* LABEL ======================================= */
.label-input-element 				 {
  color:#999; 
  font-size:18px;
  font-weight:normal;
  position:absolute;
  pointer-events:none;
  left:5px;
  top:10px;
  transition:0.2s ease all; 
  -moz-transition:0.2s ease all; 
  -webkit-transition:0.2s ease all;
}

/* active state */
.input-text-input-element:focus ~ .label-input-element, .input-text-input-element:valid ~ .label-input-element 		{
  top:-20px;
  font-size:14px;
  color:#5264AE;
}
